import Vue from 'vue'
import Router from 'vue-router'
import upload from '@/components/upload'
import viz from '@/components/viz'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/upload',
      name: 'upload',
      component: upload
    },
    {
      path: '/viz',
      name: 'viz',
      component: viz
    }
  ]
})



