<template>
  <div class="">
    <el-table :data="tableData" stripe style="width: 96%;margin-left:2%">
      <el-table-column prop="action" label="项目">
      </el-table-column>
      <el-table-column prop="money" label="金额">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { upload_url as url } from "../assets/js/url";

export default {
  name: 'viz',
  data() {
    return {
      tableData: []
    }
  },
  mounted: function() {
    var vm = this;
    var data = {
      key: 'action,money',
      condition: ''
    }
    this.$ajax.get(url + '/sql/select', {
      params: data
    }).then(function(res) {
      vm.tableData = res.data
    }).catch(function(error) {
      console.log(error);
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



