<template>
  <div id="app">
    <div class="nav">
      <el-button v-for="(key,index) in nav" :key="key.name" :type=" key.active?'success':'info' " :icon="key.icon" @click="gotoPage(key.id)">{{key.name}}</el-button>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
    return {
      nav: [
        {
          name: "数据上传",
          id: "upload",
          icon: "upload",
          active: true
        },
        {
          name: "数据分析",
          id: "viz",
          icon: "document",
          active: false
        }
      ]
    }
  },
  methods: {
    gotoPage: function(id) {
      this.$router.push({ name: id });
      for (let i = 0; i < this.nav.length; i++) {
        this.nav[i].active = (id == this.nav[i].id);
      }
    }
  },
  mounted: function() {
    for (let i = 0; i < this.nav.length; i++) {
      if (this.nav[i].active) {
        this.$router.push({ name: this.nav[i].id });
      }
    }
  }
}
</script>

<style>
html,
body {
  margin: 0;
  padding: 0;
  background: rgba(230, 230, 230, 0.5);
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  width: 100%;
  height: 600px;
  margin: 0px auto 0 auto;
  padding: 20px 0;
  text-align: center;
  overflow: auto;
}

.nav {
  margin: 10px 0 10px 0;
}
</style>



